import "./AdminView.css";

function AdminView() {


    async function getOrders() {
        await fetch("https://tiny-taco-server.herokuapp.com/yomamas/")
            .then((response) => response.json())
            .then((dataArr) => displayOrders(dataArr));
    }

    function displayOrders(dataArr) {
            dataArr.map(element => {
                return (
                    <h2>Order #{element.name}</h2>
                )})
    }
return (
<div>
        <button onClick = {() => getOrders()}>Get Orders</button>
        <h2>Current Orders: </h2>
</div>
    ) 
}

export default AdminView;
